import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import "./css/catalogue.css";
import { FiShoppingCart } from "react-icons/fi";

function Catalogue() {
  const [beers, setBeers] = useState([]);
  const [breweries, setBreweries] = useState([]);
  const [selectedBrewery, setSelectedBrewery] = useState("");
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [cartCount, setCartCount] = useState(0);
  const [showForm, setShowForm] = useState(false);
  const [newBeer, setNewBeer] = useState({
    name: "",
    description: "",
    price: "",
    brewery_id: "",
    image_url: "", // Ajout du champ image_url
  });
  const [editBeer, setEditBeer] = useState(null);

  // Charger les bires et les brasseries depuis l'API
  useEffect(() => {
    const fetchData = async () => {
      try {
        const beerResponse = await fetch("https://back-ubeers.onrender.com/beers/");
        const breweryResponse = await fetch("https://back-ubeers.onrender.com/breweries/");

        if (!beerResponse.ok || !breweryResponse.ok) {
          throw new Error("Erreur lors du chargement des donnes.");
        }

        const beerData = await beerResponse.json();
        const breweryData = await breweryResponse.json();

        setBeers(beerData);
        setBreweries(breweryData);
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  // Charger le compteur de panier depuis localStorage
  useEffect(() => {
    const storedCart = JSON.parse(localStorage.getItem("cart")) || [];
    setCartCount(storedCart.length);
  }, []);

  // Fonction pour ajouter une bire au panier
  const addToCart = (beer) => {
    const storedCart = JSON.parse(localStorage.getItem("cart")) || [];
    const updatedCart = [...storedCart, beer];
    localStorage.setItem("cart", JSON.stringify(updatedCart));
    setCartCount(updatedCart.length);
  };

  // Fonction pour modifier une bire
  const handleEditBeer = (beer) => {
    setEditBeer(beer);
    setShowForm(true);
    setNewBeer({
      name: beer.name,
      description: beer.description,
      price: beer.price,
      brewery_id: beer.brewery_id,
      image_url: beer.image_url || "",  // Ajout de l'url de l'image
    });
  };

  // Fonction pour supprimer une bire
  const handleDeleteBeer = async (beerId) => {
    try {
      const response = await fetch(`http://127.0.0.1:5000/beers/${beerId}`, {
        method: "DELETE",
      });

      if (response.ok) {
        // Mettre  jour l'tat local pour supprimer la bire de l'affichage
        setBeers((prevBeers) => prevBeers.filter((beer) => beer.id !== beerId));
      } else {
        throw new Error("Erreur lors de la suppression de la bire");
      }
    } catch (err) {
      setError(err.message);
    }
  };

  // Fonction pour envoyer le formulaire d'ajout/dition de bire
  const handleFormSubmit = async (e) => {
    e.preventDefault();

    const url = editBeer ? `https://back-ubeers.onrender.com/beers/${editBeer.id}` : "https://back-ubeers.onrender.com/beers/";
    const method = editBeer ? "PUT" : "POST";

    try {
      const response = await fetch(url, {
        method: method,
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(newBeer),
      });

      if (response.ok) {
        if (editBeer) {
          const updatedBeer = await response.json();
          setBeers(
            beers.map((beer) =>
              beer.id === editBeer.id ? updatedBeer : beer
            )
          );
        } else {
          const newBeerData = await response.json();
          setBeers([...beers, newBeerData]);
        }
        setShowForm(false);
        setEditBeer(null);
      } else {
        throw new Error("Erreur lors de l'ajout ou de la modification de la bire");
      }
    } catch (err) {
      setError(err.message);
    }
  };

  const handleBreweryChange = (e) => {
    setSelectedBrewery(e.target.value);
  };

  const filteredBeers = selectedBrewery
    ? beers.filter((beer) => beer.brewery_id === parseInt(selectedBrewery))
    : beers;

  return (
    <div className="catalogue-container">
      <nav className="navbar">
        <h1>Notre Catalogue de Bires</h1>
        <div className="left-container">
          <button className="add-beer-button" onClick={() => setShowForm(true)}>
            Ajouter une bire
          </button>
          <div className="cart-logo">
            <Link to="/cart">
              <FiShoppingCart size={24} />
              <span className="cart-count">{cartCount}</span>
            </Link>
          </div>
        </div>
      </nav>

      {loading && <p>Chargement en cours...</p>}
      {error && <p className="error">{error}</p>}

      {/* Formulaire d'ajout de bire */}
      {showForm && (
        <div className="beer-form-container">
          <h2>{editBeer ? "Modifier la bire" : "Ajouter une nouvelle bire"}</h2>
          <form onSubmit={handleFormSubmit}>
            <label>
              Nom de la bire :
              <input
                type="text"
                name="name"
                value={newBeer.name}
                onChange={(e) => setNewBeer({ ...newBeer, name: e.target.value })}
                required
              />
            </label>
            <label>
              Description :
              <textarea
                name="description"
                value={newBeer.description}
                onChange={(e) => setNewBeer({ ...newBeer, description: e.target.value })}
              />
            </label>
            <label>
              Prix :
              <input
                type="number"
                name="price"
                value={newBeer.price}
                onChange={(e) => setNewBeer({ ...newBeer, price: e.target.value })}
                step="0.01"
                required
              />
            </label>
            <label>
              Brasserie :
              <select
                name="brewery_id"
                value={newBeer.brewery_id}
                onChange={(e) => setNewBeer({ ...newBeer, brewery_id: e.target.value })}
                required
              >
                <option value="">Slectionnez une brasserie</option>
                {breweries.map((brewery) => (
                  <option key={brewery.id} value={brewery.id}>
                    {brewery.name}
                  </option>
                ))}
              </select>
            </label>
            <label>
              Image URL :
              <input
                type="text"
                name="image_url"
                value={newBeer.image_url}
                onChange={(e) => setNewBeer({ ...newBeer, image_url: e.target.value })}
              />
            </label>
            <button type="submit">{editBeer ? "Mettre  jour la bire" : "Ajouter la bire"}</button>
            <button type="button" onClick={() => setShowForm(false)}>
              Annuler
            </button>
          </form>
        </div>
      )}

      {/* Filtre par brasserie */}
      <div className="filter-container">
        <label htmlFor="brewery-select">Filtrer par brasserie :</label>
        <select
          id="brewery-select"
          value={selectedBrewery}
          onChange={handleBreweryChange}
        >
          <option value="">Toutes les brasseries</option>
          {breweries.map((brewery) => (
            <option key={brewery.id} value={brewery.id}>
              {brewery.name}
            </option>
          ))}
        </select>
      </div>

      {/* Affichage des bires */}
      <div className="beer-grid">
        {filteredBeers.map((beer) => (
          <div key={beer.id} className="beer-card">
            <h3>{beer.name}</h3>
            <img
              src={beer.image_url || "https://via.placeholder.com/150"}
              alt={beer.name}
              className="beer-image"
            />
            <p><strong>Description :</strong> {beer.description || "Non spcifie"}</p>
            <p><strong>Prix :</strong> {beer.price ? `${beer.price} ` : "Non spcifi"}</p>
            <button className="add-to-cart-button" onClick={() => addToCart(beer)}>
              Ajouter au panier
            </button>
            <button className="edit-button" onClick={() => handleEditBeer(beer)}>
              Modifier
            </button>
            <button className="delete-button" onClick={() => handleDeleteBeer(beer.id)}>
              Supprimer
            </button>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Catalogue;
